import React, { useCallback } from "react"
import styles from './materialUI.module.sass'

import DotItem from '../../component/dotItem/DotItem'
import Title from '../../component/title/Title'
import SubTitle from '../../component/subTitle/SubTitle'
import Code from '../../component/code/Code'
import Menu from '../../component/menu/Menu'
import Divider from '../../component/divider/Divider'
import Block from '../../component/block/Block'
import Hyperlink from '../../component/hyperlink/Hyperlink'
import Npm from '../../component/npm/Npm'
import CodeResult from '../../component/codeResult/CodeResult'

import "../../mock"
import useStore from "../../zustand/useStore"

function ChooseMenu() {
    const menuList = {
        prev: {
            title: 'Mock.js', to: '/mock'
        },
        next: {
            title: 'Material UI', to: '/materialUI'
        }
    }

    return (
        <Menu data={menuList} />
    )
}

function ZustandEx() {
    const { count, add } = useStore()

    return (
        <div>
            <p style={{'marginBottom': '10px', 'textAlign': 'center'}}>{count}</p>
            <div className={`btn ${styles.btn}`} onClick={() => add()}>Add</div>
        </div>
    )
}

function ZustandEx02() {
    const { fetch, list } = useStore()

    return (
        <div>
            <div>
                {list.map((item, index) => (
                    <p style={{'textAlign': 'center'}} key={index}>{item.id} : {item.name}</p>
                ))}
            </div>
            <div className={`btn ${styles.btn}`} style={{'marginTop': '20px'}} onClick={() => fetch('/api/mockTestList')}>fetch</div>
        </div>
    )
}

function ZustandEx03() {
    const { setMemberList, memberList } = useStore()

    const getList = useCallback(() => {
        setMemberList([{
                id: 0, name: 'Vivi'
            }, {
                id: 1, name: ''
            }])
    }, [])
    
    

    return (
        <div>
            <div>
                {memberList?.map((item, index) => (
                    <p style={{'textAlign': 'center'}} key={index}>{item.id} : {item.name}</p>
                ))}
            </div>
            <div className={`btn ${styles.btn}`} style={{'marginTop': '20px'}} onClick={() => getList()}></div>
        </div>
    )
}

function Mock() {
    return (
        <div>
            <Title txt="Zustand" />
            <div>
                <SubTitle txt="#  Zustand" />
                <DotItem txt={' hook '} />
            </div>
            <div>
                <SubTitle txt="# Zustand " />
                <Npm npmTxt="npm install zustand" />
            </div>
            <div>
                <SubTitle txt="# Zustand  -  1" />
                <DotItem txt={' useStore.js '} />
                <Code codeUrl="7c59d0b11915d38df3fff88df51ea8b0"/>
                <DotItem txt={' component  useStore.js '} />
                <Code codeUrl="5fd2ab1d8bd9a2b9c76f80f02aa07049"/>
                <CodeResult code={<ZustandEx />} />
            </div>
            <Block />
            <div>
                <SubTitle txt="# Zustand  -  2" />
                <DotItem txt={' useStore.js '} />
                <pre className="line-numbers">
                    <code className="language-javascript" data-prismjs-copy="" data-prismjs-copy-success="">
{`
    import create from "zustand"
    import axios from 'axios'
                        
    const useStore = create((set) => ({
        //  component 
        setMemberList: (data) => {
            set({ memberList: data })
        }
    }))
                        
    export default useStore
`}
                    </code>
                </pre>
                {/* <Code codeUrl="7c59d0b11915d38df3fff88df51ea8b0"/> */}
                <DotItem txt={' component  useStore.js '} />
                {/* <Code codeUrl="5fd2ab1d8bd9a2b9c76f80f02aa07049"/> */}
                <pre className="line-numbers">
                    <code className="language-javascript" data-prismjs-copy="" data-prismjs-copy-success="">
{`
    const { setMemberList, memberList } = useStore()

    const getList = useCallback(() => {
        setMemberList([{
            id: 0, name: 'Vivi'
            }, {
            id: 1, name: ''
        }])
    }, [])
          
    return (
        <div>
            <div>
                {memberList?.map((item, index) => (
                    <p style={{'textAlign': 'center'}} key={index}>{item.id} : {item.name}</p>
                ))}
            </div>
            <div className={styles.btn} style={{'marginTop': '20px'}} onClick={() => getList()}></div>
        </div>
    )
`}
                    </code>
                </pre>
                <CodeResult code={<ZustandEx03 />} />
            </div>
            <Block />
            <Divider />
            <div>
                <SubTitle txt="# Zustand " />
                <DotItem txt={' useStore.js '} />
                <Code codeUrl="5fd1841d02f4e9b05bbe335582b7ce3a"/>
                <DotItem txt={' component  useStore.js '} />
                <Code codeUrl="a97bdd767f5ae24105538343bd163d77"/>
                <CodeResult code={<ZustandEx02 />} />
            </div>
            <Block />
            <Divider />
            <div>
                <SubTitle txt="# " />
                <div>
                    <Hyperlink linkUrl="https://blog.yyisyou.tw/1059000a/" txt="zustand - Fluxhook" />
                </div>
            </div>
            <ChooseMenu />
        </div>
    )
}

export default Mock