import React from "react"

import DotItem from '../../component/dotItem/DotItem'
import Title from '../../component/title/Title'
import SubTitle from '../../component/subTitle/SubTitle'
import Code from '../../component/code/Code'
import Menu from '../../component/menu/Menu'
import Divider from '../../component/divider/Divider'
import Block from '../../component/block/Block'
import Hyperlink from '../../component/hyperlink/Hyperlink'
import Npm from '../../component/npm/Npm'

function ChooseMenu() {
    const menuList = {
        prev: {
            title: 'Router Mode', to: '/routerMode'
        },
        next: {
            title: 'Redux - ', to: '/redux-state'
        }
    }

    return (
        <Menu data={menuList} />
    )
}

function InstallRouter() {
    return (
        <div>
            <Title txt=" React Redux" />
            <div>
                <SubTitle txt="#  redux" />
                <DotItem txt={'Redux '} />
            </div>
            <div>
                <SubTitle txt="# redux " />
                <DotItem txt={' store.js'} />
                <DotItem txt={' <Privider>  App  store '} />
                <DotItem txt={' slice states()  reducers()'} />
                <DotItem txt={''} />
            </div>
            <Block />
            <Divider />
            <div>
                <SubTitle txt="# redux " />
                <Npm npmTxt="npm install @reduxjs/toolkit" />
                <Npm npmTxt="npm install redux" />
                <Npm npmTxt="npm install react-redux" />
            </div>
            <div>
                <SubTitle txt="# redux " />
                <DotItem txt={' src / store.js '} />
                <Code codeUrl="c1e4fa3ffcb97678c3d3d0530914cc77"/>
                <DotItem txt={' src / redux / todoSlice.js '} />
                <Code codeUrl="79eee59dd46934ffd5c92af914e74a8b"/>
                <DotItem txt={' src / index.js  <Provider>'} />
                <Code codeUrl="b6d13b87d964de5f4088e9b1449ab65c"/>
            </div>
            <Block />
            <Divider />
            <div>
                <SubTitle txt="# " />
                <div>
                    <Hyperlink linkUrl="https://penueling.com/%E6%8A%80%E8%A1%93%E7%AD%86%E8%A8%98/react-react-redux-redix-toolkit-%E6%96%B0%E6%89%8B%E6%95%99%E5%AD%B8/" txt="React + React-Redux + Redux-Toolkit " />
                </div>
            </div>
            <ChooseMenu />
        </div>
    )
}

export default InstallRouter