import React from "react"
import styles from './gitPagesBug.module.sass'

import DotItem from '../../component/dotItem/DotItem'
import Title from '../../component/title/Title'
import SubTitle from '../../component/subTitle/SubTitle'
import Code from '../../component/code/Code'
import Menu from '../../component/menu/Menu'
import Divider from '../../component/divider/Divider'
import Block from '../../component/block/Block'
import Hyperlink from '../../component/hyperlink/Hyperlink'
import Img from '../../component/img/Img'

import img13 from '../../assets/images/13.jpg'

function ChooseMenu() {
    const menuList = {
        prev: {
            title: ' git pages', to: '/gitPages'
        },
        next: {
            title: 'Axios', to: '/axios'
        }
    }

    return (
        <Menu data={menuList} />
    )
}

function GitPagesBug() {
    return (
        <div className={styles.gitPagesBug}>
            <Title txt="git pages  bug " />
            <div>
                <SubTitle txt="#  git pages " />
                <DotItem txt={' 404'} />
                <DotItem txt={'public '} />
            </div>
            <Block />
            <Divider />
            <div>
                <SubTitle txt="#  404" />
                <DotItem txt={' browserHistory  HashRouter'} />
                <Code codeUrl="6ac3aac7972b8a9f83457a802c2b676f"/>
                <Img imgUrl={img13} />
                {/* 2: 404404 */}
                {/* https://blog.yyisyou.tw/8357407a/ */}
            </div>
            <Block />
            <Divider />
            <div>
                <SubTitle txt="# public " />
                <DotItem txt={'  '} />
                <div className={styles.publicErr}>
                    <DotItem txt={' .env  README.md '} />
                    <DotItem txt={' REACT_APP '} />
                    <DotItem txt={''} />
                    <Code codeUrl="fe03d7615c55b032c0c9868f414c78c8"/>
                </div>
                <DotItem txt={' '} />
                <div className={styles.publicErr}>
                    <DotItem txt={'.env '} />
                    <DotItem txt={'.env.local '} />
                    <DotItem txt={'.env.development .env.test  .env.production '} />
                </div>
                <DotItem txt={' '} />
                <div className={styles.publicErr}>
                    <DotItem txt={'npm start  .env.development  .env.local  .env'} />
                    <DotItem txt={'npm run build  .env.production  .env.local  .env'} />
                </div>
            </div>
            <Block />
            <Divider />
            <div>
                <SubTitle txt="# " />
                <div>
                    <Hyperlink linkUrl="https://medium.com/@williamhuang_96928/%E9%83%A8%E7%BD%B2%E5%9C%A8github-pages%E7%9A%84%E4%BD%9C%E5%93%81-%E9%87%8D%E6%95%B4%E5%BE%8C404%E7%9A%84%E5%95%8F%E9%A1%8C-30f3b4800d73" txt="GitHub Pages404" />
                </div>
                <div>
                    <Hyperlink linkUrl="https://medium.com/itsoktomakemistakes/create-react-app-custom-global-variables-452caa132b95" txt=" CRA(Create-React-App) " />
                </div>    
            </div>
            <ChooseMenu />
        </div>
    )
}

export default GitPagesBug