import React from "react"

import DotItem from '../../component/dotItem/DotItem'
import Title from '../../component/title/Title'
import SubTitle from '../../component/subTitle/SubTitle'
import Menu from '../../component/menu/Menu'
import Divider from '../../component/divider/Divider'
import Block from '../../component/block/Block'
import Hyperlink from '../../component/hyperlink/Hyperlink'

function ChooseMenu() {
    const menuList = {
        prev: {
            title: 'Router Hook', to: '/router-hook/123'
        },
        next: {
            title: ' redux', to: '/redux-install'
        }
    }

    return (
        <Menu data={menuList} />
    )
}

function RouterMode() {
    return (
        <div>
            <Title txt="Router Mode" />
            <div>
                <SubTitle txt="# BrowserRouter" />
                <DotItem txt={'server  response'} />
                <DotItem txt={' GET '} />
            </div>
            <Block />
            <Divider />
            <div>
                <SubTitle txt="# HashRouter" />
                <DotItem txt={' url '} />
                <DotItem txt={' url "#" http://127.0.0.1:5500/home/#/page1'} />
                <DotItem txt={'SPA '} />
            </div>
            <Block />
            <Divider />
            <div>
                <SubTitle txt="# " />
                <div>
                    <Hyperlink linkUrl="https://ithelp.ithome.com.tw/articles/10226056" txt="React.js - 27  - react-router-dom ()" />
                </div>
                <div>
                    <Hyperlink linkUrl="https://stackoverflow.com/questions/51974369/what-is-the-difference-between-hashrouter-and-browserrouter-in-react" txt="React  HashRouter  BrowserRouter " />
                </div>
            </div>
            <ChooseMenu />
        </div>
    )
}

export default RouterMode