import React from "react"
import PropTypes from 'prop-types'

import DotItem from '../../component/dotItem/DotItem'
import Tips from '../../component/tips/Tips'
import Title from '../../component/title/Title'
import SubTitle from '../../component/subTitle/SubTitle'
import Code from '../../component/code/Code'
import Menu from '../../component/menu/Menu'
import Divider from '../../component/divider/Divider'
import Block from '../../component/block/Block'
import Hyperlink from '../../component/hyperlink/Hyperlink'
import CodeResult from '../../component/codeResult/CodeResult'

function ChooseMenu() {
    const menuList = {
        prev: {
            title: 'clsx', to: '/clsx'
        },
        next: {
            title: ' {...props}', to: '/prop2'
        }
    }

    return (
        <Menu data={menuList} />
    )
}

function Welcome(props) {
    return (
      <h1>Hello, {props.name}</h1>
    )
}

Welcome.propTypes = {
    name: PropTypes.string.isRequired
}

function Prop() {
    return (
        <div>
            <Title txt="Component  Prop" />
            <p> component  JavaScript  function, props</p>
            <Tips txt={['Component ']} />
            <div>
                <SubTitle txt="# Function Component" />
                <Code codeUrl="d3f1fb96cdc1134d5a483fa5dffae141"/>
                <CodeResult code={<Welcome name="Vivi" />} />
                <DotItem txt={' {name: "Vivi"}  props  Welcome component'} />
                <DotItem txt={' Welcome component  <h1>Hello, Vivi</h1>  element '} />
            </div>
            <Block />
            <Divider />
            <div>
                <SubTitle txt="# " />
                <div>
                    <Hyperlink linkUrl="https://zh-hant.reactjs.org/docs/components-and-props.html" txt="Components  Props - React" />
                </div>
            </div>
            <ChooseMenu />
        </div>
    )
}

export default Prop