import { createSlice } from "@reduxjs/toolkit"
export const navListSlice = createSlice({
  name: "navList",
  initialState: {
    clickNavBool: false,
    navList: [{
        title: 'CRA',
        content: [{
            txt: ' React', to: 'install'
        }]
    },{
        title: '',
        content: [{
            txt: 'sass', to: 'sass'
        },{
            txt: 'eslint', to: 'eslint'
        },{
            txt: 'clsx', to: 'clsx'
        }]
    },{
        title: 'Component',
        content: [{
            txt: 'Component  prop', to: 'prop'
        },{
            txt: ' {...prop}', to: 'prop2'
        },{
            txt: 'prop-types', to: 'prop-types'
        }]
    },{
        title: 'Hooks',
        content: [{
            txt: 'Hook ', to: 'hook'
        },{
            txt: 'useState', to: 'useState'
        },{
            txt: 'useEffect', to: 'useEffect'
        },{
            txt: 'useCallback', to: 'useCallback'
        },{
            txt: 'useContext', to: 'useContext'
        },{
            txt: 'useRef', to: 'useRef'
        },{
            txt: 'useImperativeHandle', to: 'useImperativeHandle'
        }]
    },{
        title: 'router',
        content: [{
            txt: ' router', to: 'router'
        },{
            txt: '', to: 'routerNested'
        },{
            txt: 'Router Hook', to: 'router-hook/123'
        },{
            txt: 'Router Mode', to: 'routerMode'
        }]
    },{
        title: 'redux',
        content: [{
            txt: ' redux', to: 'redux-install'
        },{
            txt: ' /  ', to: 'redux-state'
        },{
            txt: '', to: 'redux-asyncState'
        },{
            txt: ' Axios ', to: 'redux-axios'
        }]
    },{
        title: ' git pages',
        content: [{
            txt: '', to: 'gitPages'
        },{
            txt: 'bug ', to: 'gitPagesBug'
        }]
    },{
        title: '',
        content: [{
            txt: 'Axios', to: 'axios'
        },{
            txt: 'Mock js', to: 'mock'
        },{
            txt: 'Zustand', to: 'zustand'
        },{
            txt: 'Material UI', to: 'materialUI'
        },{
            txt: 'Swiper', to: 'swiper'
        },{
            txt: '',  to: 'font'
        }]
    }]
  },
  reducers: {
    changeClickState:(state, action) => {
        state.clickNavBool = action.payload
    },
    initClickBool: (state) => {
        state.clickNavBool = false
    },
  }
})

export const { changeClickState, initClickBool } = navListSlice.actions
export default navListSlice.reducer