import React from "react"
import styles from './gitPages.module.sass'

import DotItem from '../../component/dotItem/DotItem'
import Title from '../../component/title/Title'
import SubTitle from '../../component/subTitle/SubTitle'
import Code from '../../component/code/Code'
import Menu from '../../component/menu/Menu'
import Divider from '../../component/divider/Divider'
import Block from '../../component/block/Block'
import Hyperlink from '../../component/hyperlink/Hyperlink'
import Npm from '../../component/npm/Npm'
import Img from '../../component/img/Img'

import img09 from '../../assets/images/09.jpg'
import img10 from '../../assets/images/10.jpg'
import img11 from '../../assets/images/11.jpg'
import img12 from '../../assets/images/12.jpg'

function ChooseMenu() {
    const menuList = {
        prev: {
            title: 'Redux -  Axios ', to: '/redux-axios'
        },
        next: {
            title: 'git pages - bug ', to: '/gitPagesBug'
        }
    }

    return (
        <Menu data={menuList} />
    )
}

function GitPages() {
    return (
        <div className={styles.gitPages}>
            <Title txt="git pages " />
            <div>
                <SubTitle txt="#  git pages" />
                <DotItem txt={'Github '} />
            </div>
            <Block />
            <Divider />
            <div>
                <SubTitle txt="# " />
                <Npm npmTxt="npm install --save gh-pages" />
            </div>
            <div>
                <SubTitle txt="# " />
                <DotItem txt={' package.json  homepage '} />
                <div className={styles.jsonList}>
                    <DotItem txt={'myusername  github '} />
                    <DotItem txt={'my-app  Github '} />
                </div>
                <Code codeUrl="840cbb798e3a5a3927baf058c373a7a7"/>
                <Img imgUrl={img09} />
                <DotItem txt={' package.json  scripts '} />
                <Code codeUrl="98ad75ed5876c0d7e2413068625aa2ea"/>
            </div>
            <div>
                <SubTitle txt="# " />
                <DotItem txt={' Github Page '} />
                <Npm npmTxt="npm run deploy" />
                <DotItem txt={' Github  gh-pages '} />
                <Img imgUrl={img10} />
                <DotItem txt={' Settings / Pages  gh-pages '} />
                <Img imgUrl={img11} />
                <DotItem txt={' Github Pages '} />
                <Img imgUrl={img12} />
            </div>
            <Block />
            <Divider />
            <div>
                <SubTitle txt="# " />
                <div>
                    <Hyperlink linkUrl="https://ithelp.ithome.com.tw/articles/10228423" txt="[Day 29]  Github Pages " />
                </div>
            </div>
            <ChooseMenu />
        </div>
    )
}

export default GitPages