import React from "react"

import DotItem from '../../component/dotItem/DotItem'
import Title from '../../component/title/Title'
import SubTitle from '../../component/subTitle/SubTitle'
import Menu from '../../component/menu/Menu'
import Divider from '../../component/divider/Divider'
import Block from '../../component/block/Block'
import Hyperlink from '../../component/hyperlink/Hyperlink'

function ChooseMenu() {
    const menuList = {
        prev: {
            title: 'Prop-Types', to: '/prop-types'
        },
        next: {
            title: 'useState', to: '/useState'
        }
    }

    return (
        <Menu data={menuList} />
    )
}

function Hook() {
    return (
        <div>
            <Title txt="Hook " />
            <div>
                <SubTitle txt="#  Hook" />
                <DotItem txt={'React 16.8 '} />
                <DotItem txt={' class Component  state  React '} />
            </div>
            <div>
                <SubTitle txt="# Hook " />
                <DotItem txt={' Hook forif  function  Hook'} />
                <DotItem txt={' React Function  Hook'} />
                <DotItem txt={' class component '} />
            </div>
            <Block />
            <Divider />
            <div>
                <SubTitle txt="# " />
                <div>
                    <Hyperlink linkUrl="https://zh-hant.reactjs.org/docs/hooks-intro.html" txt=" Hook - React" />
                </div>
            </div>
            <ChooseMenu />
        </div>
    )
}

export default Hook