import React from "react"

import Tips from '../../component/tips/Tips'
import SubTitle from '../../component/subTitle/SubTitle'
import Title from '../../component/title/Title'
import Npm from '../../component/npm/Npm'
import Hyperlink from '../../component/hyperlink/Hyperlink'
import Img from '../../component/img/Img'
import Menu from '../../component/menu/Menu'
import Divider from '../../component/divider/Divider'
import Block from '../../component/block/Block'

import img0 from '../../assets/images/0.jpg'

function NodeTips() {
    const nodeLink = <Hyperlink linkUrl="https://nodejs.org/zh-tw/download/" txt="Node.js" />
    const tipsTxt = [ ' npm , ', nodeLink, '' ]
    
    return (
        <Tips txt={tipsTxt} />
    )
}

function ChooseMenu() {
    const menuList = {
        prev: {
            title: '', to: ''
        },
        next: {
            title: 'sass', to: '/sass'
        }
    }

    return (
        <Menu data={menuList} />
    )
}

function Install() {
    return (
        <div>
            <Title txt=" React" />
            <div>
                <SubTitle txt="#  React " />
                <NodeTips />
                <p></p>
                <Npm npmTxt="npx create-react-app my-app" />
                <p>, , </p>
                <Npm npmTxt="npm start" />
                <p><Hyperlink linkUrl="http://localhost:3000/" txt="http://localhost:3000/" /></p>
                <Img imgUrl={img0} />
            </div>
            <Block />
            <Divider />
            <div>
                <SubTitle txt="# " />
                <div>
                    <Hyperlink linkUrl="https://create-react-app.dev/" txt="Create React App" />
                </div>
            </div>
            <ChooseMenu />
        </div>
    )
}

export default Install