import React from "react"
import styles from './reduxState.module.sass'

import DotItem from '../../component/dotItem/DotItem'
import Title from '../../component/title/Title'
import SubTitle from '../../component/subTitle/SubTitle'
import Code from '../../component/code/Code'
import Menu from '../../component/menu/Menu'
import Divider from '../../component/divider/Divider'
import Block from '../../component/block/Block'
import Hyperlink from '../../component/hyperlink/Hyperlink'
import CodeResult from '../../component/codeResult/CodeResult'
import Npm from '../../component/npm/Npm'
import Img from '../../component/img/Img'

import TodoAxiosEx from '../../component/test/reduxTest/TodoAxiosView'
import img08 from '../../assets/images/08.gif'

function ChooseMenu() {
    const menuList = {
        prev: {
            title: 'Redux - ', to: '/redux-asyncState'
        },
        next: {
            title: ' git pages', to: '/gitPages'
        }
    }

    return (
        <Menu data={menuList} />
    )
}

function ReduxState() {
    return (
        <div>
            <Title txt="Redux -  Axios " />
            <div>
                <SubTitle txt="# Redux " />
                <DotItem txt={'createAsyncThunk Redux  Promise '} />
                <DotItem txt={'extraReducers Redux case reducer  createSlice '} />
                <div className={styles.img}>
                    <Img imgUrl={img08} />
                </div>
            </div>
            <Block />
            <Divider />
            <div className={styles.reduxState}>
                <SubTitle txt="# " />
                <DotItem txt={' Axios'} />
                <Npm npmTxt="npm install axios" />
                <DotItem txt={' json '} />
                <Code codeUrl="e394067b3da7894fbc949864d8e88a67"/>
            </div>
            <Block />
            <Divider />
            <div>
                <SubTitle txt="# Axiosjson " />
                <DotItem txt={' axios  json '} />
                <Code codeUrl="a3bc3d59835bd68d0115132c9115bf0f"/>
                <DotItem txt={' useSelector '} />
                <Code codeUrl="fae994b4fee9bda72b361146d561a005"/>
                <CodeResult code={<TodoAxiosEx />} />
            </div>
            <Block />
            <Divider />
            <div>
                <SubTitle txt="# " />
                <div>
                    <Hyperlink linkUrl="https://www.freecodecamp.org/news/redux-for-beginners-the-brain-friendly-guide-to-redux/" txt="Redux  -  Redux " />
                </div>
                <div>
                    <Hyperlink linkUrl="https://redux-toolkit.js.org/api/createAsyncThunk" txt="createAsyncThunk | Redux " />
                </div>
                <div>
                    <Hyperlink linkUrl="https://redux-toolkit.js.org/api/createslice#extrareducers" txt="extraReducers | Redux " />
                </div>
            </div>
            <ChooseMenu />
        </div>
    )
}

export default ReduxState