import React, { useEffect } from "react"
import { Link } from "react-router-dom"
import axios from 'axios'

import DotItem from '../../component/dotItem/DotItem'
import Title from '../../component/title/Title'
import SubTitle from '../../component/subTitle/SubTitle'
import Code from '../../component/code/Code'
import Menu from '../../component/menu/Menu'
import Divider from '../../component/divider/Divider'
import Block from '../../component/block/Block'
import Hyperlink from '../../component/hyperlink/Hyperlink'
import Npm from '../../component/npm/Npm'
import CodeResult from '../../component/codeResult/CodeResult'

function ChooseMenu() {
    const menuList = {
        prev: {
            title: 'git pages  bug ', to: '/gitPagesBug'
        },
        next: {
            title: 'Mock js', to: '/mock'
        }
    }

    return (
        <Menu data={menuList} />
    )
}

function AxiosEx() {
    const [data, setData] = React.useState([])

    useEffect(() => {
        axios.get(`${process.env.REACT_APP_BASE_URL}/data/dataList.json`).then((response) => {
            setData(response.data.titleData)
        })
    }, [])

    return (
        <div>
            {data.map((item, index) => (
                <p key={index}>{item.title} : {item.txt}</p>
            ))}
        </div>
    )
}

function Axios() {
    const moveTop = () => {
        window.scrollTo(0, 0)
    }

    return (
        <div>
            <Title txt="Axios" />
            <div>
                <SubTitle txt="#  Axios" />
                <DotItem txt={' HTTP '} />
            </div>
            <div>
                <SubTitle txt="# Axios " />
                <Npm npmTxt="npm install axios" />
            </div>
            <div>
                <SubTitle txt="# Axios " />
                <Code codeUrl="14bad2903ed8c0efd2e0abf716d4152b"/>
                <CodeResult code={<AxiosEx />} />
                <SubTitle txt="# Redux  Axios " />
                <Link to='/redux-axios' onClick={() => {moveTop()}} className='goUrlLink'>
                    <div className='arrow'></div>
                    <p className='txt'>React -  /   todoList </p>
                </Link>
            </div>
            <Block />
            <Divider />
            <div>
                <SubTitle txt="# " />
                <div>
                    <Hyperlink linkUrl="https://www.freecodecamp.org/news/how-to-use-axios-with-react/" txt=" React  Axios (2021)" />
                </div>
            </div>
            <ChooseMenu />
        </div>
    )
}

export default Axios