import React, { useState, useRef } from "react"
import styles from './useRef.module.sass'

import DotItem from '../../component/dotItem/DotItem'
import Title from '../../component/title/Title'
import SubTitle from '../../component/subTitle/SubTitle'
import Code from '../../component/code/Code'
import Menu from '../../component/menu/Menu'
import Divider from '../../component/divider/Divider'
import Block from '../../component/block/Block'
import Hyperlink from '../../component/hyperlink/Hyperlink'
import CodeResult from '../../component/codeResult/CodeResult'

function ChooseMenu() {
    const menuList = {
        prev: {
            title: 'useContext', to: '/useContext'
        },
        next: {
            title: 'useImperativeHandle', to: '/useImperativeHandle'
        }
    }

    return (
        <Menu data={menuList} />
    )
}

// useRef  input  
function UseRefEx() {
    const [inputAccTxt, setInputAccTxt] = useState('')
    const [inputPassTxt, setInputPassTxt] = useState('')
    const [selectChooseTxt, setSelectChooseTxt] = useState('Choose')

    const inputAcc = useRef('null')
    const inputPass = useRef('null')
    const selectChoose = useRef('Choose')

    //  array  ref
    const refArr = useRef([inputAcc, inputPass, selectChoose])

    return (
        <div className={styles.useRefEx}>
            <h2>{`${inputAccTxt}`}</h2>
            <h2>{`${inputPassTxt}`}</h2>
            <h2>{`Choose${selectChooseTxt}`}</h2>
            <div className={styles.inputList}>
                <input type='text' name="acc" ref={inputAcc} />
                <input type='text' name="pass" ref={inputPass} />
            </div>
            <div className={styles.chooseList}>
                <p>Choose</p>
                <select ref={selectChoose} name="choose">
                    <option>Choose</option>
                    <option value={'A'}>A</option>
                    <option value={'B'}>B</option>
                    <option value={'C'}>C</option>
                </select>
            </div>
            <div className={styles.btn} onClick={()=>{
                refArr.current.forEach((item)=>{
                    const name = item.current.name
                    const value = item.current.value
                    if (name === 'acc') {
                        setInputAccTxt(value)
                    } else if (name === 'pass') {
                        setInputPassTxt(value)
                    } else {
                        setSelectChooseTxt(value)
                    }
                })
            }}></div>
        </div>
    )
}

function UseRefEx02() {
    const inputRef = useRef(null)

    return (
        <div className={styles.useRefEx02}>
            <input type='text' ref={inputRef} />
            <div className={`btn ${styles.btn}`} onClick={()=>{
                inputRef.current.focus()
            }}>Focus</div>
        </div>
    )
}

function UseRef() {
    return (
        <div>
            <Title txt=" - useRef" />
            <div>
                <SubTitle txt="# useRef " />
                <DotItem txt={',  DOM'} />
                <DotItem txt={' JSX  DOM , '} />
                <DotItem txt={',  current '} />
            </div>
            <div>
                <SubTitle txt="# useRef " />
                <Code codeUrl="1a97c4d30cebd1bda0f421fb8f48566e"/>
                <DotItem txt={'{ current: "" }'} />
                <DotItem txt={'.currentinput'} />
            </div>
            <Block />
            <Divider />
            <div>
                <SubTitle txt="# useRef " />
                <Code codeUrl="394c5b4d7e1f047bf1ee97a242fe7440"/>
                <CodeResult code={<UseRefEx />} />
                <Code codeUrl="6b596f16a77b1affb5515a8604e4de34"/>
                <CodeResult code={<UseRefEx02 />} />
            </div>
            <Block />
            <Divider />
            <div>
                <SubTitle txt="# " />
                <div>
                    <Hyperlink linkUrl="https://ithelp.ithome.com.tw/articles/10246939" txt="Day.15React - useRef" />
                </div>
            </div>
            <ChooseMenu />
        </div>
    )
}

export default UseRef