import React from "react"
import styles from './reduxState.module.sass'

import DotItem from '../../component/dotItem/DotItem'
import Title from '../../component/title/Title'
import SubTitle from '../../component/subTitle/SubTitle'
import Code from '../../component/code/Code'
import Menu from '../../component/menu/Menu'
import Divider from '../../component/divider/Divider'
import Block from '../../component/block/Block'
import Hyperlink from '../../component/hyperlink/Hyperlink'
import CodeResult from '../../component/codeResult/CodeResult'
import Img from '../../component/img/Img'

import TodoListEx from '../../component/test/reduxTest/TodoListView'
import img07 from '../../assets/images/07.gif'

function ChooseMenu() {
    const menuList = {
        prev: {
            title: ' redux', to: '/redux-install'
        },
        next: {
            title: 'Redux - ', to: '/redux-asyncState'
        }
    }

    return (
        <Menu data={menuList} />
    )
}

function ReduxState() {
    return (
        <div>
            <Title txt="Redux - " />
            <div className={styles.reduxState}>
                <SubTitle txt="# Redux " />
                <DotItem txt={'state'} />
                <DotItem txt={'reducer state '} />
                <DotItem txt={'action state  action  reducer'} />
                <div className={styles.img}>
                    <Img imgUrl={img07} />
                </div>
            </div>
            <Block />
            <Divider />
            <div>
                <SubTitle txt="# todoList " />
                <DotItem txt={' store  state  slice '} />
                <Code codeUrl="171086ff2be480b11883f81da50c57b0"/>
            </div>
            <div>
                <SubTitle txt="# redux " />
                <DotItem txt={' useSelector  store  state'} />
                <Code codeUrl="2d39153763863f8a89223182a68d3c96"/>
            </div>
            <div>
                <SubTitle txt="# redux " />
                <DotItem txt={' useDispatch  actions '} />
                <Code codeUrl="e1a2f03174b740cbc7fd8d7f2b4fed5b"/>
                <CodeResult code={<TodoListEx />} />
            </div>
            <Block />
            <Divider />
            <div>
                <SubTitle txt="# " />
                <div>
                    <Hyperlink linkUrl="https://penueling.com/%E6%8A%80%E8%A1%93%E7%AD%86%E8%A8%98/react-react-redux-redix-toolkit-%E6%96%B0%E6%89%8B%E6%95%99%E5%AD%B8/" txt="React + React-Redux + Redux-Toolkit " />
                </div>
                <div>
                    <Hyperlink linkUrl="https://kaochihyu.coderbridge.io/2021/01/25/how-to-use-redux/" txt="Redux " />
                </div>
            </div>
            <ChooseMenu />
        </div>
    )
}

export default ReduxState