import styles from './eslint.module.sass'
import React from "react"

import Title from '../../component/title/Title'
import SubTitle from '../../component/subTitle/SubTitle'
import Npm from '../../component/npm/Npm'
import Img from '../../component/img/Img'
import Code from '../../component/code/Code'
import Menu from '../../component/menu/Menu'
import Block from '../../component/block/Block'
import Divider from '../../component/divider/Divider'
import Hyperlink from '../../component/hyperlink/Hyperlink'

import img02 from '../../assets/images/02.jpg'
import img03 from '../../assets/images/03.jpg'

function ChooseMenu() {
    const menuList = {
        prev: {
            title: 'sass', to: '/sass'
        },
        next: {
            title: 'clsx', to: '/clsx'
        }
    }

    return (
        <Menu data={menuList} />
    )
}

function Eslint() {
    return (
        <div className={styles.install}>
            <Title txt="Eslint" />
            <div>
                <SubTitle txt="# Eslint " />
                <Npm npmTxt="npm install eslint --save-dev" />
                <Block />
                <SubTitle txt="#  EsLint " />
                <Npm npmTxt="eslint --init" />
                <p className={styles.txt}> ESLint , </p>
                <div className={styles.problemList}>
                    <p className={styles.txt}> 1.  ESLint </p>
                    <p className={styles.txt}> 2. </p>
                    <p className={styles.txt}> 3.  modules</p>
                    <p className={styles.txt}> 4.  TypeScript</p>
                    <p className={styles.txt}> 5. </p>
                    <p className={styles.txt}> 6.  config </p>
                    <Img imgUrl={img02} />
                </div>
            </div>
            <Block />
            <div>
                <SubTitle txt="# EsLint " />
                <p className={styles.txt}>  package.json </p>
                <Code codeUrl="e4fd41ddab8ea9619f1d5bab0d6a161e"/>
                <p className={styles.txt}>  .eslintrc.json </p>
                <p className={styles.txt}> Ex </p>
                <Code codeUrl="324e236dfb1b9499c557e51ccbca733f"/>
            </div>
            <Block />
            <div>
                <SubTitle txt="#   " />
                <Npm npmTxt="npm run lint" />
                <Img imgUrl={img03} />
            </div>
            <Block />
            <Divider />
            <div>
                <SubTitle txt="# " />
                <div>
                    <Hyperlink linkUrl="https://eslint.org/" txt="ESlint - Pluggable JAvaScript linter" />
                </div>
                <div>
                    <Hyperlink linkUrl="https://ithelp.ithome.com.tw/articles/10215259" txt="Day02 | , ,  ESLint" />
                </div>
            </div>
            <ChooseMenu />
        </div>
    )
}

export default Eslint